import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { SummaryListening } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'summary-spoken-text',
  templateUrl: './summary-spoken-text.component.html',
  styleUrls: ['./summary-spoken-text.component.css']
})
export class SummarySpokenTextComponent implements OnInit {

  createSummary: FormGroup = this.formBuilder.group({
    summaryName: ["", [Validators.required]],
    summaryType: ["", [Validators.required]],
    contentKeyword: ["", [Validators.required]],
    vocabularyWord: ["", [Validators.required]],
    contentKeywordLimit: ["", [Validators.required]],
    maxWordLimit: ["", [Validators.required]],
    audioFile: [null, Validators.required]
  });

  // any
  isErrText: String = null;
  public summaryId: any;
  public editorDataQestion: any = "";
  public editorDataAdvise: any = "";
  // boolean
  public isCreate: boolean = false;
  public submitted: boolean = false;
  public isProgress: boolean = false;
  public isTouchQuestion: boolean = false;
  public isTouchAdvise: boolean = false;
  isToast: boolean = false;
  public subscription: Subscription;
  public questionCode: string;

  public typeList: any = [
    {
      _id: "SPOKEN",
      name: "SPOKEN",
    },
  ];
  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private dataService: SaveService,
  ) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

  }

  get f() {
    return this.createSummary.controls;
  }

  getMarkDownForSummaryQuestion(e) {
    this.editorDataQestion = e;
    this.isTouchQuestion = true;
  }
  getMarkDownForAdviseSummary(e) {
    this.editorDataAdvise = e;
    this.isTouchAdvise = true;
  }
  onCreate() {
    this.submitted = true;
    if (this.createSummary.invalid) {
      this.isProgress = false;
      return;
    } else {

      let summaryObj = new SummaryListening();
      summaryObj.name = this.createSummary.get("summaryName").value;
      summaryObj.type = this.createSummary.get("summaryType").value;
      summaryObj.question = this.editorDataQestion
        ? this.editorDataQestion.innerHTML
        : "";
      summaryObj.contentKeywords = this.createSummary.get(
        "contentKeyword"
      ).value;
      summaryObj.vocabularyWords = this.createSummary.get(
        "vocabularyWord"
      ).value;
      summaryObj.maxContentKeywords = Number(
        this.createSummary.get("contentKeywordLimit").value
      );
      summaryObj.maxWordLimit = Number(
        this.createSummary.get("maxWordLimit").value
      );
      summaryObj.adviseContent = this.editorDataAdvise
        ? this.editorDataAdvise.innerHTML
        : "";
      summaryObj.audio = this.createSummary.get('audioFile').value;
      this.subscription = this.dataService.OnSaveSummarySpokenText(summaryObj, this.questionCode, this.summaryId).subscribe(
        () => {
          this.goBack()
        });
    }
  }

  goBack() {
    this.dataService.goBackPrev();
  }
  


  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
